#navigation-div {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
}

#logo img {
    width: 120px;
    height: 50px;
    filter: invert(100%);
    margin-top: 10px;
    padding: 2px;
    padding-bottom: 5px;
    transition: all 500ms;
}

#logo img:hover {
    background: #fff;
    border-radius: 10px;
    filter: invert(0%);
    transform: scale(1.2);
}

#navbar ul {
    display: flex;
    list-style: none;
    margin-top: 10px;
}

#navbar ul a {
    text-decoration: none;
    padding: 2px 25px;
    font-size: 2rem;
    color: #fff;
    cursor: pointer;
    transition: all 300ms;
}

#navbar ul a:hover {
    background-color: #fff;
    border-radius: 10px;
    color: #000;
}

#navigation-div::before {
    content: "";
    background: url('../img/bg1.jpg') no-repeat center center/cover;
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 80px;
    z-index: -1;
    opacity: 0.8;
}

@media only screen and (max-width:1170px){
    #navbar ul a{
        padding:2px 6px;
        font-size: 1.1rem;
    }
    #navigation-div::before{
        height: 110px;
    }
    #logo img{
        width:100px;
        height: 35px;
        margin-top: 10px;
        padding: 2px;
        padding-bottom: 5px;
    }
    #logo img:hover {
        transform: scale(1.05);
    }
}